import { Component, OnInit } from '@angular/core';
import { Http, Headers } from '@angular/http';

@Component({
  selector: 'app-forms-server',
  templateUrl: './forms-server.component.html',
  styleUrls: ['./forms-server.component.css']
})
export class FormsServerComponent implements OnInit {

  public temp;
  constructor(private http: Http) { }

  ngOnInit() {
    this.http.get('http://localhost:8000/')
      .toPromise()
      .then(res => res.json())
      .then(json => {
        this.temp = json;
      });

  }
  postToExpress() {
    const url = 'http://localhost:8000/sing';
    const headers = new Headers({ 'Content-type': 'application/json' });
    const body = JSON.stringify({ 'name': 'Khoa Pham' });
    this.http.post(url, body, { headers })
      .toPromise()
      .then(res => res.json())
      .then(json => {
        this.temp = json;
      });
  }
}
